*{
    margin: 0;
   padding: 0;
}


.navbar-container ul   {
    display: flex;
    z-index: 100;
    position: relative;
    width: 100%;
}
.desktop-nav ul a { 
    display: inline-flex;
    font-size: calc(1vw + .2vh);
    padding-left: .1em;
    padding-right: .1em;
    list-style: none;
    width: 100%;
    justify-content: center;
    padding-top: .6em;
    min-height: 5vh;
    max-height: 30vh;
    text-decoration: inherit;
}


.desktop-nav li img {
    height: 2em;
}
@media screen and (max-width: 1024px){
    .desktop-nav ul  li  {
        font-size: calc(1vw + .7vh);
        padding-top: .5em ;
        padding-left: .5em;
    }
}
@media screen and (max-width: 600px){
    .desktop-nav ul{
        align-items: stretch;
    }
    .desktop-nav ul a {
        align-items: center;
    }
}
@media screen and (max-width: 420px){
    .desktop-nav ul a li{
        padding: .5em;
        font-size: calc(1vw + 1vh);
    }
    
    .desktop-nav a li img{
        height: 1.8em;
        padding-top: .8em;
    }
    .desktop-nav ul a{
        padding-top: 0;
    }
}
