.dot-general1{
    background: #58ba91bd;
    border-radius: 50%;
    display: flex;
    position: absolute;
    -webkit-animation: breathing 5s ease-out infinite normal;
    animation: breathing 5s ease-out infinite normal;
    z-index: 10000;
}
.dot-general2{
    background: #58ba91d3;
    border-radius: 50%;
    display: flex;
    position: absolute;
    -webkit-animation: breathing 7s ease-out infinite normal;
    animation: breathing 7s ease-out infinite normal;
    z-index: 10000;
}
.dot-button{
    background: #58ba91d3;
    border-radius: 50%;
    display: flex;
    position: absolute;
    -webkit-animation: dot-breathing 3s ease-out infinite normal;
    animation: dot-breathing 3s ease-out infinite normal;
    z-index: 10000;
}
.dot-general3{
    background: #58ba91d3;
    border-radius: 50%;
    display: flex;
    position: absolute;
    -webkit-animation: square 60s infinite;
    animation:         square 60s infinite;
    
    -webkit-transition-timing-function: linear;
    transition-timing-function: linear;
    
}

@-webkit-keyframes square {
    0%   { transform: translateY(0); }
    100% { transform: translateY(-500px); }
  }
  @keyframes square {
    0%   { transform: translateY(0); }
    100% { transform: translateY(-700px); }
  }
