.timeline {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    
    height: max-content;
    overflow: auto;
}

/* Timeline - Years */
.timelines-years {
   margin: 0;
    margin-top: 200px;
    padding: 0 0 0 200px;
    white-space: nowrap;
    
    list-style: none;
   /* Fix display: inline-block spacing issue */
    font-size: 0;
}
.timelines-years li {
    position: relative;
    top: -6px;
    display: inline-block;
    width: 400px;
    color: #868686;
    font-size: 11px;
    line-height: 11px;
    text-indent: -12px;
}
/* Display last year */
.timelines-years li:last-child {
    width: 100px;
}
/* Timeline - Events */
.timeline-events {
    margin-top: 200px;
    padding: 0;
    list-style: none;
    white-space: nowrap;
   /* Fix display: inline-block spacing issue */
    font-size: 0;
}
.timeline-events h2, .timeline-events h3, .timeline-events h4 {
    margin: 0 0 1px 0;
    font-weight: normal;
    font-size: 14px;
}
.timeline-events h2 {
    color: #777;
    text-transform: uppercase;
}
.timeline-events h4 {
    color: #000;
    font-style: italic;
}
.timeline-events li {
    position: relative;
    display: inline-block;
}
.timeline-events li:before {
    position: absolute;
    left: 0;
    bottom: -36px;
    height: 8px;
    border-radius: 8px;
    content: '';
}
.timeline-events li:nth-child(1) {
    bottom: 0px;
}
.timeline-events li:nth-child(2) {
    bottom: 16px;
}
.timeline-events li:nth-child(3) {
    bottom: 32px;
}
.timeline-events li:nth-child(4) {
    bottom: 48px;
}
.timeline-events li:nth-child(5) {
    bottom: 64px;
}
.timeline-events li:nth-child(6) {
   bottom: 80px;
}
.timeline-events li:nth-child(7) {
    bottom: 96px;
 }
 .timeline-events li:nth-child(8) {
    bottom: 112px;
 }
 .timeline-events li:nth-child(9) {
    bottom: 128px;
 }
/* Timeline - Events - Colours */
/* Grey/Green */
.timeline-events li:nth-child(1n):before {
    background: #ac9bc0;
    border-radius: 0 6px 6px 0;
}
.timeline-events li:nth-child(1n) h3 {
    color: #ac9bc0;
}
/* Orange */
.timeline-events li:nth-child(2n):before {
    background: #e4c143;
}
.timeline-events li:nth-child(2n) h3 {
    color: #e4c143;
}
/* Blue */
.timeline-events li:nth-child(3n):before {
    background: #57b990;
}
.timeline-events li:nth-child(3n) h3 {
    color: #57b990;
}
/* Red */

/* Aqua/Grey */
.timeline-events li:nth-child(4):before {
    background: #545454;
}
.timeline-events li:nth-child(4) h3 {
    color: #545454;
}
/* Timeline - Events - Grid */
/* X years */

/* X years */

.timeline-event-years-2, .timeline-event-years-2:before {
    width: 300px;
}


/* Timeline - Legend */
.timeline-event-legend {
    position: relative;
}
.timeline-event-legend span {
    position: absolute;
    bottom: -150px;
    left: 850px;
}
.timeline-event-legend h2, .timeline-event-legend h3, .timeline-event-legend h4 {
    display: inline;
    margin-right: 10px;
}
.timeline-event-legend i {
    position: absolute;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-bottom-color: #fff;
}
.timeline-event-legend i:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-top-color: #fff;
}
.timeline-event-legend > i {
    bottom: -32px;
    right: -12px;
}
.timeline-event-legend > i:after {
    left: -4px;
    bottom: -12px;
}
.timeline-event-legend span > i {
    top: -1px;
    left: -14px;
}
.timeline-event-legend span > i:after {
    left: -4px;
    bottom: -12px;
}
