.manifest-container{
    display: flex;
    position: relative;
    align-items: flex-start;
    justify-content: center;
    height: max-content;   
    border-radius: 0 0 40px;
    box-shadow: 8px 0 5px -6px  black;
    background-color: #ededed;
    font-size: calc(.9vw + 1vh);
}
.manifest-wrapper{
    display: flex;
    position: relative;
    flex-direction: column;
    height: 100%;
    max-width: 1900px;
    width: 80%;
    align-self: center;
    justify-self: center;
    padding-top: 5vh;
    padding-left: 5vw;
    padding-right: 5vw;
    padding-bottom: 7vh;
}
.manifest-wrapper h5{
    font-size: calc(1.9vw + 2.6vh);
    padding-bottom: 1em;
    color: #272727;
}
.manifest-wrapper p{
    color: #3b3b3b;
    text-indent: 1em;
}
@media screen and (max-width: 1024px){
    .manifest-wrapper{
        width: 80%;
    }
}
@media screen and (max-width: 600px){
   
    .manifest-wrapper p{
        font-size:calc(.9vw + 1.2vh) ;
    }
}
.manifest-container a{
    color: rgb(156, 156, 156);
    text-decoration: none;
}
.manifest-container a:hover{
    color: rgb(78, 78, 78);
    text-decoration: underline;
}